/* Section Feauture */

.s-feauture {
	position: relative;
	max-height: 850px;
	background-repeat: no-repeat;
	background-size: cover;
	background-position: top center;
	padding-top: 6.875rem;
	@include lg(max-height, 733px);
	@include md(max-height, 634px);
	@include md(padding-top, 5.5rem);
	@include sm(max-height, 585px);
	@include sm(padding-top, 4rem);
	@include xs(max-height, 471px);
	&::before {
		content: "";
		display: block;
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		background-color: rgba(255, 255, 255, 0.85); } }

.picture-wrap {
	position: relative;
	max-width: 43.75rem;
	height: 41.75rem;
	margin: 0 auto;
	@include lg(max-width, 35.9rem);
	@include lg(height, 34.9rem);
	@include md(max-width, 31rem);
	@include md(height, 30rem);
	@include sm(max-width, 28rem);
	@include sm(height, 27rem);
	@include xs(max-width, 22rem);
	@include xs(height, 22rem); }

.display-one {
	position: absolute;
	max-width: 29.625rem;
	max-height: 36.125rem;
	left: 12%;
	bottom: 0;
	@include lg(max-width, 23.375rem);
	@include md(max-width, 20rem);
	@include sm(max-width, 18rem);
	@include xs(max-width, 14rem); }

.display-two {
	position: absolute;
	max-width: 13.4375rem;
	height: auto;
	left: 67%;
	bottom: -67px;
	@include lg(max-width, 10.25rem);
	@include lg(left, 65%);
	@include lg(bottom, -35px);
	@include md(max-width, 8.5rem);
	@include md(bottom, -22px);
	@include sm(max-width, 7.5rem);
	@include sm(bottom, -15px);
	@include xs(max-width, 5.95rem); }
