/* Section About-us */

.s-about-us {
	min-height: 750px;
	padding-top: 5.75rem;
	padding-bottom: 6.875rem;
	text-align: center;
	@include sm(padding-bottom, 5rem);
	@include sm(padding-top, 4rem);
	@include sm(padding-bottom, 4rem);
	&__photos-wrap {
		margin-top: 4.625rem;
		@include sm(margin-top, 2rem); } }

.photos-wrap {
	@include row-flex();
	justify-content: center; }

.photo-block {
	@include col();
	@include size(4);
	@include size-xs(12);
	min-width: 380px;
	@include xs(min-width, 290px);
	margin-top: 1.5rem;
	text-align: center;
	&__wrap-content {
		background-color: $aqua; }
	&__link {
		display: block;
		position: relative;
		z-index: 10;
		@include trs(0.5s);
		&:hover,
		&:focus {
			transform: translate(-10px, -10px); }
		&::before {
			content: "\f500";
			padding-top: 95px;
			font: {
				family: "Font Awesome 5 Free";
				size: 1.5em;
				weight: 700; }
			color: #fff;
			@include gradient-bg;
			opacity: 0;
			@include trs(0.5s); }
		&::after {
			content: "super team";
			position: absolute;
			top: 52%;
			left: 50%;
			transform: translate(-50%, 0);
			font: {
				family: $title-font;
				weight: bold;
				size: 1.125em; }
			color: #ffffff;
			text-transform: uppercase;
			opacity: 0;
			@include trs(0.5s); }
		&:hover::before,
		&:focus::before,
		&:hover::after,
		&:focus::after {
			opacity: 0.9; } }
	&__img {
		display: block;
		width: 100%;
		height: 100%; } }

/* Section Facts */

.s-facts {
	background-color: $aqua; }

.facts-wrap {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between; }

.facts-block {
	width: 20%;
	padding-top: 4.9rem;
	padding-bottom: 5.25rem;
	text-align: center;
	border-left: 1px solid rgba(255, 255, 255, 0.3);
	font-family: $title-font;
	@include md(width, 30%);
	@include md(border-left, none);
	@include xs(width, 100%);
	@include xs(padding-bottom, 4rem);
	@include xs(padding-top, 4rem);
	@include xs(border-bottom, 1px solid rgba(255, 255, 255, 0.3));
	&:nth-child(5) {
		border-right: 1px solid rgba(255, 255, 255, 0.3);
		@include md(border-right, none);
		@include xs(border-bottom, none); }
	&:nth-child(1) {
		@include md(padding-bottom, 0);
		@include xs(padding-bottom, 4rem); }
	&:nth-child(2),
	&:nth-child(3) {
		@extend .facts-block:nth-child(1); }
	&__count {
		font-size: 4.5em;
		font-weight: bold;
		line-height: 1;
		color: #fff; }
	&__text {
		margin-top: 15px;
		font-size: 0.875em;
		font-weight: normal;
		text-transform: uppercase;
		color: #fff; } }

