/* Section Service */

.s-service {
	padding-top: 6.7rem;
	padding-bottom: 5.2rem;
	@include sm(padding, 4rem 0); }

.service-wrap {
	position: relative;
	margin-top: 2.75rem;
	@include row-flex();
	justify-content: space-around;
	@include lg(justify-content, space-between);
	@include sm(justify-content, center);
	@include xs(margin-top, 1.75rem);
	&::before {
		content: "";
		display: block;
		position: absolute;
		top: 182px;
		left: 0;
		width: 100%;
		height: 1px;
		background-color: #e5e5e5;
		@include lg(display, none); } }

.service-block {
	@include col-padding();
	@include size(4);
	@include size-lg(6);
	@include size-xs(12);
	@include md(padding-right, 0);
	@include md(padding-left, 0);
	@include md(margin-right, 15px);
	@include md(margin-left, 15px);
	@include sm(padding-bottom, 0);
	@include sm(min-width, 290px);
	@include sm(padding-top, 2.5rem);
	@include sm(text-align, center);
	min-width: 350px;
	padding-top: 3.75rem;
	padding-bottom: 2.25rem;
	&__img-block {
		float: left;
		width: 32px;
		height: 32px;
		margin-right: 28px;
		@include sm(float, none);
		@include sm(margin, 0 auto 25px auto); }
	&__text-block {
		float: left;
		max-width: 17.5rem;
		@include sm(float, none);
		@include sm(margin, 0 auto); }
	&__text {
		margin-top: 12px;
		font: {
			family: inherit;
			size: 0.9375em; }
		color: #999; } }
