.s-people-say {
	position: relative;
	min-height: 51.875rem;
	margin-top: 5.5rem;
	padding-top: 6.875rem;
	padding-bottom: 3.625rem;
	background-repeat: no-repeat;
	background-size: cover;
	background-position: top center;
	@include sm(padding-top, 4.25rem);
	@include sm(padding-bottom, 1rem);
	@include sm(margin-top, 4.25rem);
	&::before {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		background-color: rgba(255, 255, 255, 0.85); } }

.clients-wrap {
	@include row-flex();
	justify-content: space-between;
	@include md(justify-content, center);
	@include sm(margin-top, 5rem);
	margin-top: 8rem;
	margin-left: 0;
	margin-right: 0; }

.client-block {
	@include col();
	@include size(6);
	@include row-flex();
	justify-content: space-between;
	@include size-md(8);
	@include md(justify-content, center);
	@include size-sm(10);
	@include sm(margin-bottom, 3rem);
	@include size-xs(12);
	margin-left: 15px;
	margin-right: 15px;
	@include lg(margin-left, -15px);
	@include lg(margin-right, -15px);
	margin-bottom: 4.25rem;
	&__img {
		@include col();
		@include size(3);
		min-width: 6.875rem;
		height: auto;
		& img {
			border-radius: 100%; } }
	&__feedback {
		@include col();
		@include size(9);
		@include size-md(12);
		@include md(text-align, center);
		position: relative;
		&::before {
			content: "";
			position: absolute;
			top: 43%;
			left: 0;
			width: 3.75rem;
			height: 3px;
			background-color: #f38181;
			@include md(display, none); } }
	&__name {
		@extend .h3;
		margin-top: 8px;
		margin-bottom: 9px;
		@include sm(margin-bottom, 4px); }
	&__prof {
		@extend .team-member__position;
		color: #333; }
	&__text {
		margin-top: 2.25rem;
		font-size: 0.9375em;
		@include sm(margin-top, 1.2rem); } }
